.home-page #section2 h1, #section3 h1, #section4 h1{
    left:5%;
    position: relative;
    transition: all 1s cubic-bezier(.45,-0.19,.25,1.22);
    transition-delay: .2s;
    opacity: 0;
    transform: skewX(-23deg);
   
}
.home-page #section2 h1 span, #section3 h1 span, #section4 h1 span{
    right:25%;
    position: relative;
    transition: all 1s cubic-bezier(.45,-0.19,.25,1.22);
    transition-delay: .2s;
    opacity: 0;
    transform: skewX(80deg);
   
}
.home-page #section2 .buttonCombo-warp, #section3 .buttonCombo-warp, #section4 .buttonCombo-warp{
    bottom:30%;
    transition: all .7s cubic-bezier(.45,-0.19,.25,1.22);
    opacity: 0;
    transform: scale(.8) translateX(-50%);
    transition-delay: .2s;
    backface-visibility: hidden;
   
}
.home-page.fp-viewing-1 #section2 h1, .home-page.fp-viewing-2 #section3 h1, .home-page.fp-viewing-3 #section4 h1

{
left:0%;
opacity: 1;
transform: skewX(0deg);
}
.home-page.fp-viewing-1 #section2 h1 span, .home-page.fp-viewing-2 #section3 h1 span, .home-page.fp-viewing-3 #section4 h1 span{
right: 0%;
opacity: 1;
transform: skewX(0deg);
}

.home-page.fp-viewing-1 #section2 .buttonCombo-warp, .home-page.fp-viewing-2 #section3 .buttonCombo-warp, .home-page.fp-viewing-3 #section4 .buttonCombo-warp

{
bottom:7.3rem;
opacity: 1;
transform: scale(1) translateX(-50%);
@media(max-width:37.5em){
    transform: scale(1) translateX(-41%);
}
}



@keyframes logoanim {
    0%{
        top:70%;
        opacity: 0;
        height: 0;
    }

    100%{
        top:50%;
        opacity: 1;
        height: 100%;
    }

}

@keyframes sidenavanim {
    0%{
        opacity: 0;
        top:60%;
    }

    100%{
        opacity: 1;
        top:50%
    }

}

       
    
